import { players, projectiles, chatMessages, score, flag, medikits, armors } from './gameState.js';
import { walls, redSpawn, blueSpawn, classes } from './gameConfig.js';
import { checkCollision, checkBorderCollision } from './collisionUtils.js';

// Granaten-Explosion Funktion
function createExplosion(io, players, x, y, throwerId) {
    const radius = 100; // Explosionsradius
    Object.values(players).forEach(player => {
        if (player.id !== throwerId) {
            const distance = Math.sqrt(
                Math.pow(player.x - x, 2) + 
                Math.pow(player.y - y, 2)
            );
            
            if (distance <= radius) {
                // Berechne Schaden basierend auf der Entfernung
                let damage;
                if (distance <= radius / 3) {
                    damage = 3; // Voller Schaden im inneren Bereich
                } else if (distance <= radius * 2/3) {
                    damage = 2; // Mittlerer Schaden
                } else {
                    damage = 1; // Minimaler Schaden am Rand
                }
                
                player.health = Math.max(0, player.health - damage);
                if (player.health <= 0) {
                    const spawnPoint = player.team === 'red' ? redSpawn : blueSpawn;
                    player.x = spawnPoint.x;
                    player.y = spawnPoint.y;
                    player.health = 2;
                    player.armor = 0;
                    player.hasFlag = false;
                    if (flag.holder === player.id) {
                        flag.holder = null;
                        flag.x = 750;
                        flag.y = 400;
                    }
                }
            }
        }
    });
    
    // Sende ein Update an alle Clients
    io.emit('state', { players, projectiles, walls, flag, score, redSpawn, blueSpawn, medikits, armors });
}

export function setupSocketHandlers(io, gameState) {
    io.on('connection', (socket) => {
        console.log('Neuer Spieler verbunden:', socket.id);
        socket.emit('chatUpdate', chatMessages);
        socket.emit('updateMedikits', medikits);
        socket.emit('updateArmors', armors);

        socket.on('chooseTeam', (data) => {
            const { username, team, class: playerClass } = data || {};

            if (!username || !team || !playerClass) {
                console.error("‚ùå Fehler: `username`, `team` oder `class` ist undefined!", { username, team, playerClass });
                return;
            }

            players[socket.id] = {
                x: team === 'red' ? redSpawn.x : blueSpawn.x,
                y: team === 'red' ? redSpawn.y : blueSpawn.y,
                team,
                username,
                hasFlag: false,
                health: 2,
                armor: 0,
                class: playerClass,
                lastShot: 0,
                hasGrenade: false,
                speed: 5
            };

            console.log(`üì¢ ${username} ist Team ${team.toUpperCase()} als ${classes[playerClass].name} beigetreten!`);
            chatMessages.push(`üì¢ ${username} ist Team ${team.toUpperCase()} als ${classes[playerClass].name} beigetreten!`);
            io.emit('chatUpdate', chatMessages);
            io.emit('state', { players, projectiles, walls, flag, score, redSpawn, blueSpawn, medikits });
        });

        socket.on('move', (data) => {
            const player = players[socket.id];
            if (!player) return;

            const playerClass = classes[player.class];
            let speedMultiplier = playerClass.speed / 3; // Basis-Geschwindigkeit

            // Erh√∂he die Geschwindigkeit wenn Speed-Boost aktiv ist
            if (player.speedBoostActive) {
                speedMultiplier *= 2; // Verdoppele die Geschwindigkeit
            }

            const newX = player.x + data.dx * speedMultiplier;
            const newY = player.y + data.dy * speedMultiplier;

            if (!checkCollision(newX - 10, newY - 10, 20, 20, walls) && checkBorderCollision(newX, newY, 20, 20)) {
                player.x = newX;
                player.y = newY;
            }

            handleFlagPickup(socket, player);
            handleFlagCapture(socket, player, io);
            handleMedikitPickup(socket, player, io);
            handleArmorPickup(socket, player, io);

            // Check item pickups
            gameState.medikits.forEach(medikit => {
                if (medikit.active && Math.abs(player.x - medikit.x) < 30 && Math.abs(player.y - medikit.y) < 30) {
                    medikit.active = false;
                    player.health = Math.min(player.health + 50, 100);
                    io.emit('updateMedikits', gameState.medikits);
                }
            });

            gameState.armors.forEach(armor => {
                if (armor.active && Math.abs(player.x - armor.x) < 30 && Math.abs(player.y - armor.y) < 30) {
                    armor.active = false;
                    player.armor = Math.min(player.armor + 50, 100);
                    io.emit('updateArmors', gameState.armors);
                }
            });

            // Neue Item-Pickup-Logik
            gameState.grenades.forEach(grenade => {
                if (grenade.active && Math.abs(player.x - grenade.x) < 30 && Math.abs(player.y - grenade.y) < 30) {
                    grenade.active = false;
                    player.hasGrenade = true;
                    io.emit('updateGrenades', gameState.grenades);
                }
            });

            // Speed-Booster Logik
            gameState.speedBoosters.forEach(booster => {
                if (booster.active && Math.abs(player.x - booster.x) < 30 && Math.abs(player.y - booster.y) < 30) {
                    booster.active = false;
                    player.speedBoostActive = true;
                    
                    // Setze Timer f√ºr Speed-Boost-Ende
                    setTimeout(() => {
                        if (players[socket.id]) {
                            players[socket.id].speedBoostActive = false;
                            io.to(socket.id).emit('speedBoostEnded');
                        }
                    }, 20000); // 20 Sekunden Speed-Boost

                    io.to(socket.id).emit('speedBoostActive', 20000);
                    io.emit('updateSpeedBoosters', gameState.speedBoosters);
                }
            });

            io.emit('state', { players, projectiles, walls, flag, score, redSpawn, blueSpawn, medikits, armors });
        });

        socket.on('chatMessage', (msg) => {
            if (!players[socket.id]) return;
            const message = `${players[socket.id].username}: ${msg}`;
            chatMessages.push(message);
            io.emit('chatUpdate', chatMessages);
        });

        socket.on('shoot', (data) => {
            const player = players[socket.id];
            if (!player) return;

            const playerClass = classes[player.class];
            const now = Date.now();
            
            // √úberpr√ºfe Cooldown
            if (now - player.lastShot < playerClass.cooldown) {
                return;
            }

            // Erstelle Projektile basierend auf der Klasse
            for (let i = 0; i < playerClass.projectileCount; i++) {
                const spread = playerClass.spread;
                const angle = data.angle + (Math.random() - 0.5) * spread;
                
                projectiles.push({
                    x: player.x,
                    y: player.y,
                    vx: Math.cos(angle) * playerClass.projectileSpeed,
                    vy: Math.sin(angle) * playerClass.projectileSpeed,
                    owner: socket.id,
                    damage: playerClass.damage
                });
            }

            player.lastShot = now;
            io.emit('state', { players, projectiles, walls, flag, score, redSpawn, blueSpawn, medikits, armors });
        });

        socket.on('throwGrenade', data => {
            const player = players[socket.id];
            if (!player || !player.hasGrenade) {
                console.log('Granaten-Wurf fehlgeschlagen:', { 
                    playerExists: !!player, 
                    hasGrenade: player?.hasGrenade 
                });
                return;
            }

            console.log('Granaten-Wurf gestartet:', { 
                playerId: socket.id,
                playerPos: { x: player.x, y: player.y },
                angle: data.angle,
                power: data.power
            });

            // Granate wird sofort verbraucht
            player.hasGrenade = false;
            
            // Erstelle eine eindeutige ID f√ºr diese Granate
            const grenadeId = `grenade_${Date.now()}_${Math.random()}`;
            
            // Initialisiere die Granate mit Startposition und Geschwindigkeit
            const baseSpeed = 15;
            const speed = baseSpeed * (data.power || 1);
            const grenade = {
                id: grenadeId,
                x: player.x,
                y: player.y,
                dx: Math.cos(data.angle) * speed,
                dy: Math.sin(data.angle) * speed,
                throwerId: socket.id
            };

            // Sende initiale Position
            io.emit('updateGrenadePosition', {
                id: grenadeId,
                x: grenade.x,
                y: grenade.y
            });

            // Simuliere Granatenwurf
            let steps = 0;
            const maxSteps = 30;
            const gravity = 0.5;
            const interval = setInterval(() => {
                // Aktualisiere Position
                grenade.x += grenade.dx;
                grenade.y += grenade.dy;
                grenade.dy += gravity; // Schwerkraft
                
                // Verringere horizontale Geschwindigkeit (Luftwiderstand)
                grenade.dx *= 0.98;

                // Sende Position an alle Clients
                io.emit('updateGrenadePosition', {
                    id: grenadeId,
                    x: grenade.x,
                    y: grenade.y
                });

                // Pr√ºfe Kollisionen
                let hasCollided = false;

                // Kollision mit W√§nden
                walls.forEach(wall => {
                    if (!hasCollided && 
                        grenade.x >= wall.x && 
                        grenade.x <= wall.x + wall.width &&
                        grenade.y >= wall.y && 
                        grenade.y <= wall.y + wall.height) {
                        hasCollided = true;
                    }
                });

                // Kollision mit Spielfeldgrenzen
                if (!hasCollided && (
                    grenade.x < 0 || grenade.x > 1600 ||
                    grenade.y < 0 || grenade.y > 1200
                )) {
                    hasCollided = true;
                }

                // Bei Kollision oder maximaler Flugzeit: Explosion mit Verz√∂gerung
                if (hasCollided || steps >= maxSteps) {
                    clearInterval(interval);
                    
                    // Zeige die Granate f√ºr eine Sekunde am Boden
                    const finalPosition = { 
                        id: grenadeId,
                        x: grenade.x, 
                        y: grenade.y 
                    };
                    io.emit('grenadeDropped', finalPosition);
                    
                    // Warte eine Sekunde vor der Explosion
                    setTimeout(() => {
                        // Schaden an Spielern in der N√§he
                        Object.values(players).forEach(targetPlayer => {
                            if (targetPlayer.id !== grenade.throwerId) {
                                const distance = Math.sqrt(
                                    Math.pow(targetPlayer.x - finalPosition.x, 2) + 
                                    Math.pow(targetPlayer.y - finalPosition.y, 2)
                                );
                                
                                if (distance <= 100) { // Explosionsradius
                                    const damage = Math.floor(3 * (1 - distance / 100)); // 1-3 Schaden
                                    targetPlayer.health = Math.max(0, targetPlayer.health - damage);
                                    
                                    if (targetPlayer.health <= 0) {
                                        // Respawn
                                        const spawnPoint = targetPlayer.team === 'red' ? redSpawn : blueSpawn;
                                        targetPlayer.x = spawnPoint.x;
                                        targetPlayer.y = spawnPoint.y;
                                        targetPlayer.health = 100;
                                        targetPlayer.armor = 0;
                                    }
                                }
                            }
                        });

                        // Sende Explosion-Event
                        io.emit('grenadeExplosion', finalPosition);

                        // Aktualisiere Spielzustand
                        io.emit('state', { players, projectiles, walls, flag, score, redSpawn, blueSpawn, medikits, armors });
                    }, 1000); // 1 Sekunde Verz√∂gerung
                }

                steps++;
            }, 50); // Aktualisiere alle 50ms
        });

        socket.on('spawn', data => {
            const { username, team, playerClass } = data;
            const spawnPoint = team === 'red' ? redSpawn : blueSpawn;

            players[socket.id] = {
                id: socket.id,
                x: spawnPoint.x,
                y: spawnPoint.y,
                team,
                username,
                health: 100,
                armor: 0,
                class: playerClass,
                lastShot: 0,
                hasGrenade: false,
                speed: 5,
                speedBoostActive: false
            };

            io.emit('playerJoined', { id: socket.id, username, team });
        });

        socket.on('disconnect', () => {
            if (players[socket.id]) {
                const leaveMessage = `‚ùå ${players[socket.id].username} hat das Spiel verlassen.`;
                chatMessages.push(leaveMessage);
                io.emit('chatUpdate', chatMessages);
                delete players[socket.id];
            }
            console.log('Spieler getrennt:', socket.id);
            io.emit('state', { players, projectiles, walls, flag, score, redSpawn, blueSpawn, medikits, armors });
        });
    });
}

function handleFlagPickup(socket, player) {
    if (!flag.holder && Math.abs(player.x - flag.x) < 20 && Math.abs(player.y - flag.y) < 20) {
        flag.holder = socket.id;
        player.hasFlag = true;
        console.log(`üö© ${player.username} hat die Flagge aufgenommen!`);
        socket.emit('chatMessage', `üö© ${player.username} hat die Flagge aufgenommen!`);
    }
}

function handleFlagCapture(socket, player, io) {
    const spawn = player.team === 'red' ? redSpawn : blueSpawn;
    if (player.hasFlag && player.x > spawn.x && player.x < spawn.x + spawn.width && 
        player.y > spawn.y && player.y < spawn.y + spawn.height) {
        score[player.team]++;
        console.log(`üèÜ Punkt f√ºr Team ${player.team.toUpperCase()}`);

        flag.x = 750;
        flag.y = 400;
        flag.holder = null;
        player.hasFlag = false;

        io.emit('chatMessage', `üéâ ${player.username} hat einen Punkt f√ºr Team ${player.team.toUpperCase()} erzielt!`);

        if (score[player.team] === 3) {
            io.emit('gameOver', player.team);
            score.red = 0;
            score.blue = 0;
        }
    }
}

function handleMedikitPickup(socket, player, io) {
    medikits.forEach(medikit => {
        if (medikit.active && Math.abs(player.x - medikit.x) < 10 && Math.abs(player.y - medikit.y) < 10) {
            if (player.health < 2) {
                player.health = 2;
                console.log(`‚ù§Ô∏è ${player.username} hat sich geheilt!`);
                io.emit('chatMessage', `‚ù§Ô∏è ${player.username} hat sich geheilt!`);
            }
            medikit.active = false;
            io.emit('updateMedikits', medikits);
        }
    });
}

function handleArmorPickup(socket, player, io) {
    armors.forEach(armor => {
        if (armor.active && Math.abs(player.x - armor.x) < 10 && Math.abs(player.y - armor.y) < 10) {
            if (player.armor === 0) {
                player.armor = 1;
                console.log(`üõ° ${player.username} hat R√ºstung aufgenommen!`);
                io.emit('chatMessage', `üõ° ${player.username} hat R√ºstung aufgenommen!`);
            }
            armor.active = false;
            io.emit('updateArmors', armors);
        }
    });
} 